.row {
  display: flex;
  flex-wrap: wrap;
}

.row--ful-height {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

@for $i from 1 through 12 {
  .col-xs-#{$i} {
    padding: 0.5rem;
    width: 100% * ($i / 12);
  }

}

@for $i from 1 through 12 {
  @media(min-width: $screen-breakpoint-sm){
    .col-sm-#{$i}{
      padding: 0.5rem;
      width: 100% * ($i / 12);
    }
  }
}

