.intro-container {
  padding: 0 calc(100% * (1 / 14));
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 67% 100%;
  background-color:white;
  background-position: -70%;
  @include fluid-background-image("../assets/images/Hero-Image.png", "../assets/images/Hero-Image@2x.png")
}

.intro-container--form {
  padding: 0 calc(100% * (1 / 14));
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 80% 100%;
  background-color:white;
  background-position: -80%;
  @include fluid-background-image("../assets/images/Header-Form-Background.png", "../assets/images/Header-Form-Background@2x.png")
}

.intro-content{
  padding-top: 20vh;
  @extend %center--column;
}

.intro__title{
  @include fluid-type(768px, 1900px, 18px, 38px);
  text-align: center;
}

.intro__decoration{
  @extend %center
}

.intro__btn-section{
  @extend %center;
  margin-top: 65px;
}

.intro__btn-section__btn{
  @include fluid-type(768px, 1440px, 18px, 36px);
  @include btn-border($color-dark);
  @extend %btn-bkg;
  max-height: 120px;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  flex-basis: 50%;
  font-weight: 300;
  text-align: center;
  margin: 0 22px;
  color: $color-dark;
  &:hover{
    cursor: pointer;
  }
}

.intro__dsc-section{
  @extend %center;
  font-size: 40px;
  font-weight: 300;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.intro__steps-section{
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro__rectangle{
  position: relative;
  font-size: 10px;
  width: 15em;
  height: 15em;
  @extend %center--column;
  text-align: center;

  p {
    font-size: 2.4em;
  }
}

#intro__rectangle__text{
    font-size: 3.2em;
}

.rotated{
  top: 0;
  left: 0;
  position: absolute;
  font-size: 10px;
  width: 15em;
  height: 15em;
  border: 1px solid gray;
  transform: rotate(45deg);
}